export interface ICustomAppUserRole {
  readonly id: Uuid;
  readonly codename: string | null;
}

export interface ICustomAppContext {
  readonly environmentId: Uuid;
  readonly userId: string;
  readonly userEmail: string;
  readonly userRoles: ReadonlyArray<ICustomAppUserRole>;
}

export interface ICustomAppClientMessage {
  readonly type: CustomAppClientMessageType;
  readonly data?: CustomAppApi;
  readonly requestId: Uuid;
}

export interface ICustomAppHostMessage {
  readonly type: CustomAppHostMessageType;
  readonly data?: CustomAppApi;
  readonly requestId?: Uuid;
}

export interface IInitResponseData {
  readonly context: ICustomAppContext;
  readonly config: AnyObject | null;
}

export type CustomAppApi = IInitResponseData;

export enum CustomAppClientMessageType {
  InitDataRequest = 'init-data-request',
}

export enum CustomAppHostMessageType {
  InitDataResponse = 'init-data-response',
}
