import {
  CustomAppClientMessageType,
  CustomAppHostMessageType,
  ICustomAppContext,
} from '../../client/app/applications/customApps/types/CustomAppsApi.ts';
import { ICustomAppMessageSender } from './CustomAppMessageSender.ts';

export interface ICustomApp {
  readonly init: () => Promise<{ config: object | null; context: ICustomAppContext }>;
}

export class CustomApp implements ICustomApp {
  readonly #sender: ICustomAppMessageSender;

  constructor(sender: ICustomAppMessageSender) {
    this.#sender = sender;
  }

  public init(): Promise<{ config: object | null; context: ICustomAppContext }> {
    return new Promise((resolve, reject) => {
      this.#sender.sendMessageWithReply(
        CustomAppClientMessageType.InitDataRequest,
        CustomAppHostMessageType.InitDataResponse,
        (data: { config: object | null; context: ICustomAppContext }) => {
          if (data) {
            resolve(data);
          } else {
            reject(new Error('Failed to initialize the custom app'));
          }
        },
      );
    });
  }
}
